import { useEffect, useState } from 'react';

const emailRe = /^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
const passwordRe = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[+-_@$!%*?&#.,;:{}]).{8,}$/;

const useValidation = (value, type) => {
  const [validationError, setValidationError] = useState('');
  const isEmailValid = emailRe.test(String(value.toLowerCase()));
  const isPasswordValid = passwordRe.test(value);

  useEffect(() => {
    switch (type) {
      case 'username':
        if (!value) {
          setValidationError(' " "    !');
        } else if (value.length < 4) {
          setValidationError('      4 ');
        } else {
          setValidationError('');
        }
        break;
      case 'email':
        if (!value) {
          setValidationError(' ""    !');
        } else if (!isEmailValid) {
          setValidationError('   !');
        } else {
          setValidationError('');
        }
        break;
      case 'password':
        if (!value) {
          setValidationError(' ""    !');
        } else if (value.length < 8) {
          setValidationError('     8 ');
        } else if (!isPasswordValid) {
          setValidationError(
            '    , ,    '
          );
        } else {
          setValidationError('');
        }
        break;
      default:
        break;
    }
  }, [value]);

  return validationError;
};

export default useValidation;
